<template>
  <div class="modulelist">
    <el-card>
      <div slot="header">
        <span class="title">产品模块列表</span>
        <el-button type="primary" class="btn-create-module" @click="showModuleFunc">新建模块</el-button>
        <el-popover placement="right" width="500" trigger="hover">
          <div>
            <p>主要针对经常需要监控的变量情况，提供自定义报表功能。</p>
            <p>例如新建模块卡卡，点击通用监控，可以通过样本、变量和时间段的选择，对该变量波动监控。</p>
          </div>
          <i class="el-icon-question" slot="reference"></i>
        </el-popover>
      </div>
      <ul class="box-all-module clear">
        <li class="module-item" v-for="(item,index) in moduleArr" v-bind:key="index">
          <router-link
            :to="{name: 'module-view',params: {moduleid: item.id,reportid: 'none'}}"
          >{{item.name}}</router-link>
          <i class="el-tag__close el-icon-close" @click.prevent.stop="showConfirmFunc(item)"></i>
        </li>
      </ul>
      <span class="nodata el-table__empty-block" v-if="moduleArr.length===0">暂无数据</span>
    </el-card>
    <el-button type="primary" class="btn-create-module key-target" @click="keyTarget">通用监控</el-button>
    <modulename @save="createModule" :init-visible.sync="showDialogModule"></modulename>
    <dialog-confirm @confirm="delModuleFunc" :init-visible.sync="showDialogConfirm"></dialog-confirm>
  </div>
</template>

<script>
import modulename from '@/components/modulename';
import dialogConfirm from '@/components/dialog-confirm';
import {createProduct, getProductList, delProduct} from '@/api/monitor.js';

export default {
  name: 'page-modulelist',
  data() {
    return {
      showDialogModule: false,
      showDialogConfirm: false,
      moduleArr: []
    };
  },
  components: {
    modulename,
    dialogConfirm
  },
  computed: {},
  methods: {
    // 创建模块
    createModule(name) {
      if (name == null || name == '') {
        this.$message.warning('请输入模块名称！');
      } else if (this.moduleArr.some(item => item.name === name)) {
        this.$message.warning('该名称已存在，请重新命名');
      } else {
        // 如果数组中不存在该名称
        this.showDialogModule = false;
        createProduct(name)
          .then(result => {
            this.moduleArr.unshift({
              name,
              id: result.result
            });
            this.$message.success('创建成功');
          })
          .catch(err => console.log(err));
      }
    },
    showModuleFunc() {
      this.showDialogModule = true;
    },
    // 监控面板
    keyTarget() {
      this.$router.push({name: 'univariate-monitor'});
    },
    getAllModule() {
      getProductList()
        .then(result => {
          if (Array.isArray(result.result)) {
            this.moduleArr = result.result;
          }
        })
        .catch(err => console.log(err));
    },
    showConfirmFunc(item = {}) {
      this.showDialogConfirm = true;
      this.delItem = item;
    },
    delModuleFunc() {
      this.showDialogConfirm = false;
      delProduct(this.delItem.id)
        .then(result => {
          this.$message.success('删除成功');
          this.getAllModule();
        })
        .catch(err => console.log(err));
    }
  },
  created() {
    this.getAllModule();
  },
  mounted() {}
};
</script>

<style scoped>
.btn-create-module {
  margin: 0 0 0 50px;
}

.el-card {
  margin: 20px 15px;
}
.wrap-all-module {
  padding: 20px 20px 0 20px;
}

.module-item {
  float: left;
  margin: 0 40px 20px 0;
  position: relative;
}

.module-item a {
  display: block;
  padding: 0 20px;
  height: 32px;
  border: 1px solid rgba(64, 158, 255, 0.2);
  color: #409eff;
  font-size: 12px;
  text-align: center;
  line-height: 30px;
  background-color: rgba(64, 158, 255, 0.1);
  border-radius: 4px;
}

.module-item i {
  position: absolute;
  font-size: 12px;
  height: 16px;
  width: 16px;
  line-height: 16px;
  text-align: center;
  right: -7px;
  top: -7px;
  cursor: pointer;
  background-color: #409eff;
  color: #fff;
  border-radius: 50%;
  display: none;
}
.module-item:hover i {
  display: block;
}

.key-target {
  margin: 80px 20px 0 0;
  float: right;
}
</style>
