<template>
  <el-dialog title="提示" top="20vh" :visible.sync="dialogVisible" width="30%">
    <span>是否确定删除？</span>
    <span slot="footer" class="dialog-footer">
      <el-button @click="dialogVisible = false">取 消</el-button>
      <el-button type="primary" @click="confirmFunc">确 定</el-button>
    </span>
  </el-dialog>
</template>

<script>
export default {
  name: 'dialog-confirm',
  props: {
    initVisible: {
      type: Boolean,
      required: true,
      default: false
    }
  },
  computed: {
    dialogVisible: {
      get: function() {
        return this.initVisible;
      },
      set: function(newV) {
        if (!newV) {
          this.$emit('update:initVisible', newV);
        }
      }
    }
  },
  methods: {
    confirmFunc() {
      this.$emit('confirm');
    }
  }
};
</script>

